import { ref } from 'vue'
import { defineStore } from 'pinia'

export const useNavarStore = defineStore('navarStore', () => {
    const navarState = ref(false);
    const fix = ref('')
    function showNavar() {
        navarState.value = !navarState.value;      
    }
    
    function isShown (){

        navarState.value = !navarState.value;
        const descElement = document.getElementById('desc');
        
        if (descElement) {
            // Update the style using a ref or reactive data binding
            fix.value = descElement.style.marginTop = '500px';
        }
        if(navarState.value == false){
            fix.value = descElement.style.marginTop = '0px';
        }
    }
    return { navarState, showNavar,fix, isShown}
});