<template>

    <NavbarComp />

    <div class="todo-container">
      <div class="todo-content">
        <h1>Todo Application</h1>
        <div class="inputs-container">
          <div class="add-task-container">
            <input type="text" id="add-task" v-model="task" placeholder="Add Tasks" required>
            <button type="button" id="add-task" name="add-task" @click="addTask">Add Task</button>
          </div>
          <div class="empty">
            <input type="search" id="search-task" v-model="searchQuery" placeholder="Search Tasks" required>
            <div>
              <!-- Nothing Goes Here -->
            </div>
          </div>
        </div><!-- End of inputs-container -->
        <table id="main-table">
          <thead>
            <tr>
              <th>Details</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="todo in filteredTodos" :key="todo.id">
              <td>
                  <p :class="{ check: todo.status  }">{{ capitalizeFirstLetter(todo.details) }}</p>
              </td>
              <td>
                  <a href="#" class="font-medium hover:underline pr-2" @click="updateData(todo.id, todo.status)"><i class="bi bi-check-circle"></i></a>
                  <a href="#" class="font-medium hover:underline" @click="removeTask(todo.id)"><i class="bi bi-trash3"></i></a>
              </td>
            </tr>
          </tbody>
        </table><!-- End of main-table -->
        <button type="button" id="clear-task" name="clear-task" value="Clear Task" v-if="hasTasks" @click="clearAllTasks">Clear Task</button>
      </div><!-- End of todo-content -->
    </div><!-- End of todo-container -->

    <FooterComp />

</template>

<script setup>

    import NavbarComp from "../components/NavbarComp.vue";
    import FooterComp from "../components/FooterComp.vue";
    import { ref, onMounted, computed } from 'vue';
    import { db } from '../firebase';
    import { collection, addDoc, serverTimestamp, onSnapshot, orderBy, query, doc, deleteDoc, updateDoc, getDocs } from 'firebase/firestore';

    const task = ref('');
    const todos = ref([]);
    const searchQuery = ref('');
    const status = ref(false);
    const capitalizeFirstLetter = (str) => {
      return str.charAt(0).toUpperCase() + str.slice(1);
    };

    const addTask = async (e) => {
      e.preventDefault();
      try {
        await addDoc(collection(db, 'todo'), {
          details: task.value,
          status: status.value,
          date_created: serverTimestamp(),
        });
        task.value = '';
      } catch (e) {
        console.error('Error adding document: ', e);
      }
    };

    const getData = () => {
      const fsquery = query(collection(db, "todo"), orderBy('date_created','asc'));
      onSnapshot(fsquery,(querySnapshot) => {
        const newTasks = [];
        querySnapshot.forEach((doc) => {
          const datas = {
              id:doc.id,
              details:doc.data().details,
              status:doc.data().status,
            }
            newTasks.unshift(datas);
        });
        todos.value=newTasks;
      })
    };

    const updateData = (id, currentStatus) => {
      const fsquery = doc(db, 'todo', id);
      const newData = {
        status: !currentStatus,
      }
      updateDoc(fsquery, newData)
      .then(() => {
        console.log('Document successfully updated.');
      })
      .catch((error) => {
        console.error('Error updating document:', error);
      });
    };
    
    const removeTask = async (id) => {
      try{
        await deleteDoc(doc(db,'todo',id))
      }catch(e){
        console.log(e);
      }
    };

    const hasTasks = computed(()=> {
      return todos.value.length > 0;
    });

    const clearAllTasks = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, 'todo'));
        querySnapshot.forEach(async (doc) => {
          await deleteDoc(doc.ref);
        });
        todos.value = [];
      } catch (e) {
        console.log(e);
      }
    };

    const filteredTodos = computed(()=> {
      return todos.value.filter(todo => {
        return todo.details.toLowerCase().includes(searchQuery.value.toLowerCase());
      });
    });

    onMounted(()=> {
      getData()
    })
</script>

<style scoped>

    .todo-container {
      width: 50%;
      margin: 100px auto 100px auto;
      background-color: #6763E7;
      border-top-right-radius: 15px;
      border-bottom-left-radius: 15px;
      padding: 35px;
      box-shadow: 2px 2px 5px #A0A0A0;
    }

    .todo-container .todo-content {
      width: 100%;
      margin: auto;
      padding: 15px;
      background-color: #ffffff;
      color: #000000;
      border-top-right-radius: 15px;
      border-bottom-left-radius: 15px;
    }

    .todo-content h1 {
      text-align: center;
      font-family: Helvetica, Georgia, "Times New Roman";
      font-size: 22px;
      margin-bottom: 30px;
    }

    .todo-content .inputs-container {
      width: 100%;
      margin: auto;
    }

    .inputs-container .add-task-container {
      width: 60%;
      margin: auto auto 15px auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .inputs-container .empty {
      width: 60%;
      margin: auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .inputs-container .add-task-container input[type="text"] {
      display: block;
      padding: 7px 15px;
      border: 1px solid #121212;
      outline: none;
      border-radius: 3px;
    }

    .inputs-container .add-task-container input[type="text"]::placeholder,
    .inputs-container .empty input[type="search"]::placeholder {
      font-size: 12px;
    }

    .inputs-container .add-task-container button#add-task {
      display: block;
      background-color: #121212;
      color: #ffffff;
      border: none;
      outline: none;
      padding: 8px 15px;
      border-radius: 3px;
      font-family: Helvetica, Georgia, "Times New Roman";
      font-size: 13px;
    }

    .inputs-container .add-task-container button#add-task:hover {
      cursor: pointer;
      background-color: #ffffff;
      color: #000000;
      transition: all 0.4s ease 0s;
      border: 1px solid #121212;
    }

    .inputs-container input[type="search"] {
      display: block;
      padding: 7px 15px;
      border: 1px solid #121212;
      outline: none;
      border-radius: 3px;
    }

    .todo-content #main-table {
      width: 80%;
      margin: 15px auto 15px auto;
      border: 1px solid #121212;
      border-radius: 8px;
    }

    .todo-content #main-table tr th {
      background-color: #121212;
      color: #ffffff;
      padding: 8px;
      text-align: center;
    }

    .todo-content #main-table tr:nth-child(even) {
      background-color: #f2f2f2;
    }

    .todo-content #main-table tr td {
      padding: 5px;
      text-align: center;
    }

    .todo-content #main-table tr td a:first-child {
      margin-right: 25px;
    }

    .todo-content #main-table tr td a {
      display: inline-block;
    }

    .todo-content #main-table tr td a i {
      font-size: 18px;
    }

    .todo-content #main-table tr, th, td {
      border: 1px solid #121212;
    }

    .todo-content button#clear-task {
      display: block;
      width: 80%;
      margin: auto;
      border: 1px solid #FF0021;
      outline: none;
      padding: 10px 15px;
      font-size: 13px;
      font-family: Helvetica, Georgia, "Times New Roman";
      background-color: #FF0021;
      color: #ffffff;
      border-radius: 3px;
    }

    .todo-content button#clear-task:hover {
      background-color: #ffffff;
      color: #000000;
      transition: all 0.4s ease 0s;
      cursor: pointer;
      border: 1px solid #121212;
    }

    @media screen and (max-width: 768px) {
      .todo-container {
        width: 90%;
        margin: 100px auto 100px auto;
      }

      .inputs-container .add-task-container {
        flex-direction: column;
      }

      .inputs-container .add-task-container input[type="text"] {
        display: block;
        width: 100%;
        margin: auto;
      }

      .inputs-container .add-task-container button#add-task {
        display: block;
        width: 100%;
        margin: auto;
      }

      .inputs-container .empty {
        flex-direction: column;
      }
    }

</style>