<template>

    <NavbarComp />

    <div class="login-container">
        <div class="login-content">
            <h1>Welcome back!</h1>
            <p>Register to make your account exist</p>
            <form id="main-form" @submit.prevent="Register">
                <input type="text" v-model="username"  name="email" placeholder="Enter Username">
                <input type="password" v-model="userpassword" name="password" placeholder="••••••••">
                <button type="submit"  id="register" name="register" value="register">Register</button>
            </form><!-- End of main-form -->
            <div class="para-con">
                <p>Already have an account? <router-link to="/login">Signin Here</router-link></p>
            </div>
        </div><!-- End of login-content -->
    </div><!-- End of login-container -->

    <FooterComp />

</template>

<script setup>
import NavbarComp from "../components/NavbarComp.vue";
import FooterComp from "../components/FooterComp.vue";
import { db } from '../firebase';
import { ref } from 'vue';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { v4 as uuidv4 } from 'uuid';


const username = ref('');
const userpassword = ref('');


const Register = async (e) => {
    e.preventDefault();
    try {
        if (username.value == '' || username.value == null && userpassword.value == '' || userpassword.value == null) {
            return;
        }
        await addDoc(collection(db, 'chat'), {
            id: uuidv4(),
            username: username.value,
            password: userpassword.value,
            message: '',
            date_created: serverTimestamp(),
        });
        alert("Succcessfully Created!");
        location.replace('/login');
    } catch (e) {
        console.error('Unsuccessfully Created!: ', e);
    }
}
</script>

<style scoped>

    .login-container {
        width: 55%;
        margin: 100px auto 100px auto;
    }

    .login-container .login-content {
        width: 55%;
        margin: auto;
        background-color: #6763E7;
        color: #ffffff;
        border-radius: 8px;
        padding: 15px;
    }

    .login-content h1 {
        text-align: center;
        font-size: 25px;
    }

    .login-content p {
        text-align: center;
        font-size: 13.5px;
    }

    .login-content #main-form {
        width: 100%;
        margin: auto;
        padding: 15px;
    }

    #main-form input[type="text"],
    #main-form input[type="password"],
    #main-form button {
        display: block;
    }

    #main-form input[type="text"],
    #main-form input[type="password"] {
        width: 70%;
        margin: auto;
        border: none;
        outline: none;
        margin-bottom: 15px;
        padding: 8px 15px;
        color: #000000
    }

    #main-form input[type="text"]::placeholder,
    #main-form input[type="password"]::placeholder {
        font-size: 13px;
    }

    #main-form button#register {
        width: 70%;
        margin: 20px auto auto auto;
        display: block;
        padding: 12px 15px;
        background-color: #121212;
        color: #ffffff;
        font-size: 15px;
        border: none;
        outline: none;
        border-radius: 3px;
    }

    #main-form button#register:hover {
        cursor: pointer;
        background-color: #ffffff;
        color: #000000;
        transition: all 0.4s ease 0s;
    }

    .login-content .para-con {
        width: 100%;
        margin: auto;
        text-align: center;
    }

    .login-content .para-con p {
        font-size: 13.5px;
    }

    .login-content .para-con p a {
        color: #ffffff;
        text-decoration: underline;
    }

    @media screen and (max-width: 768px) {
        
        .login-container {
            width: 100%;
            margin: 100px auto 100px auto;
        }

        .login-container .login-content {
            width: 90%;
            margin: auto;
        }

        #main-form input[type="text"],
        #main-form input[type="password"] {
            width: 100%;
            margin: auto;
            border: none;
            outline: none;
            margin-bottom: 15px;
            padding: 8px 15px;
        }

        #main-form input[type="text"]::placeholder,
        #main-form input[type="password"]::placeholder {
            text-align: center;
        }

        #main-form button#register {
            width: 100%;
            margin: 20px auto auto auto;
            display: block;
            padding: 12px 15px;
            background-color: #121212;
            color: #ffffff;
            font-size: 15px;
            border: none;
            outline: none;
            border-radius: 3px;
        }
    }

</style>