<template>

    <NavbarComp />

    <div class="login-container" v-if="state.username == '' || state.username == null || state.id == '' || state.id == null">
        <div class="login-content">
            <h1>Welcome back!</h1>
            <p>Login to access your account</p>
            <form id="main-form" @submit.prevent="Login">
                <input type="text" v-model="inputUserName" name="email" placeholder="Enter Username">
                <input type="password" v-model="inputUserPassword" name="password" placeholder="••••••••">
                <button type="submit" id="login" name="login" value="login">Login</button>
            </form><!-- End of main-form -->
            <div class="para-con">
                <p>Already have an account? <router-link to="/register">Signup Here</router-link></p>
            </div>
        </div><!-- End of login-content -->
    </div><!-- End of login-container -->

    <div class="chat-app-container" v-else>
        <h1>Public Chat</h1>
        <div class="chat-app-content">
            <div class="logout-container">
                <div class="empty">
                    <p>Welcome, {{state.username}}</p>
                </div>
                <div class="right">
                    <button type="button" id="logout" name="logout" value="logout" @click="Logout">Logout</button>
                </div>
            </div>
            <div class="chat-box" ref="chatBox">
                <div class="messages" v-for="message in state.messages" :key="message.key">
                    <div class="reciever" v-if="message.username !== state.username">
                        <span>{{message.username}}</span>
                        <p>{{message.content}}</p>
                    </div>
                    <div class="sender" v-if="message.username === state.username">
                        <p>{{message.content}}</p>
                    </div>
                </div>
            </div>
            <div class="controller">
                <input type="text" v-model="inputMessages" placeholder="Type your message..." />
                <button type="button" id="send-message" name="send-message" @click="SendMessage"><i class="bi bi-send w-full"></i></button>
            </div>
        </div>
    </div><!-- End of chat-app-container -->

    <FooterComp />

</template>

<script setup>
    import NavbarComp from "../components/NavbarComp.vue";
    import FooterComp from "../components/FooterComp.vue";
    import { db } from '../firebase';
    import { reactive, ref, onMounted, } from 'vue';
    import { collection, addDoc, query, onSnapshot, serverTimestamp, orderBy } from 'firebase/firestore';
    import { v4 as uuidv4 } from 'uuid';

    const inputUserName = ref('');
    const inputUserPassword = ref('');
    const inputMessages = ref('');
    const chatBox = ref(null);

    const state = reactive({
        id: "",
        username: "",
        password: "",
        messages: [],
        udata: []
    })


    const Login = () => {
    const userQuery = query(collection(db, "chat"));

    try {
        onSnapshot(userQuery, (querySnapshot) => {
        querySnapshot.forEach((doc) => {
            const userData = {
            id: doc.id,
            username: doc.data().username,
            password: doc.data().password,
            };
            if (inputUserName.value === userData.username && inputUserPassword.value === userData.password) {
            state.id = uuidv4();
            state.username = inputUserName.value;
            state.password = inputUserPassword.value;
            inputUserName.value = "";
            inputUserPassword.value = "";
            localStorage.setItem('id', state.id);
            alert("Login Successfully!")
            }
        });
        });
        
    } catch (e) {
        console.log("There is an error", e);
    }
    };

    const SendMessage = async (e) => {
        e.preventDefault();
        try {
            if (inputMessages.value == '' || inputMessages.value == null) {
                return;
            }

            let currentId = localStorage.getItem('id');
            await addDoc(collection(db, 'chat'), {
                id: currentId,
                username: state.username,
                message: inputMessages.value,
                date_created: serverTimestamp(),
            });
            inputMessages.value = '';
        } catch (e) {
            console.error('Error adding document: ', e);
        }
    }

    const getMessages = () => {
        const fsquery = query(collection(db, "chat"), orderBy('date_created','asc'));
        onSnapshot(fsquery,(querySnapshot) => {
            const message = [];
            querySnapshot.forEach((doc) => {
            const mess = {
                id:doc.id,
                content:doc.data().message,
                username:doc.data().username,
                }
                message.push(mess);
            });
            state.messages=message;
        })
    }

    const Logout = () => {
        state.username = "";
        state.password = "";
        localStorage.clear();
    }

    onMounted(()=> {
        getMessages();
    })
</script>

<style scoped>

    .login-container {
        width: 55%;
        margin: 100px auto 100px auto;
    }

    .login-container .login-content {
        width: 55%;
        margin: auto;
        background-color: #6763E7;
        color: #ffffff;
        border-radius: 8px;
        padding: 15px;
    }

    .login-content h1 {
        text-align: center;
        font-size: 25px;
    }

    .login-content p {
        text-align: center;
        font-size: 13.5px;
    }

    .login-content #main-form {
        width: 100%;
        margin: auto;
        padding: 15px;
    }

    #main-form input[type="text"],
    #main-form input[type="password"],
    #main-form button {
        display: block;
    }

    #main-form input[type="text"],
    #main-form input[type="password"] {
        width: 70%;
        margin: auto;
        border: none;
        outline: none;
        margin-bottom: 15px;
        padding: 8px 15px;
        color: #000000
    }

    #main-form input[type="text"]::placeholder,
    #main-form input[type="password"]::placeholder {
        font-size: 13px;
    }

    #main-form button#login {
        width: 70%;
        margin: 20px auto auto auto;
        display: block;
        padding: 12px 15px;
        background-color: #121212;
        color: #ffffff;
        font-size: 15px;
        border: none;
        outline: none;
        border-radius: 3px;
    }

    #main-form button#login:hover {
        cursor: pointer;
        background-color: #ffffff;
        color: #000000;
        transition: all 0.4s ease 0s;
    }

    .login-content .para-con {
        width: 100%;
        margin: auto;
        text-align: center;
    }

    .login-content .para-con p {
        font-size: 13.5px;
    }

    .login-content .para-con p a {
        color: #ffffff;
        text-decoration: underline;
    }

    /* PENETRATION TEST */

    .chat-app-container {
        width: 60%;
        margin: 100px auto 100px auto;
        background-color: #6763E7;
        color: #ffffff;
        padding: 15px 20px;
        border-top-right-radius: 15px;
        border-bottom-left-radius: 15px;
        box-shadow: 2px 2px 5px #A0A0A0;
    }

    .chat-app-container .chat-app-content {
        width: 100%;
        margin: auto;
        background-color: #ffffff;
        color: #000000;
        border-top-right-radius: 15px;
        border-bottom-left-radius: 15px;
        padding: 15px;
    }

    .chat-app-container h1 {
        font-family: Helvetica, Georgia, "Times New Roman";
        font-size: 22px;
        text-align: center;
        margin: 20px auto 20px auto;
    }

    .chat-app-content .logout-container {
        background-color: #121212;
        width: 100%;
        margin: auto auto 15px auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 8px;
        border-radius: 8px;
    }

    .chat-app-content .logout-container .empty {
        color: #ffffff;
        margin-top: 8px;
    }

    .chat-app-content .chat-box {
        overflow-y: scroll;
        height: 40vh;
    }

    .chat-app-content .controller {
        width: 100%;
        margin: 20px auto auto auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .chat-app-content .controller input[type="text"] {
        display: block;
        border: 1px solid gray;
        outline: none;
        padding: 12px 15px;
        width: 95%;
        margin: auto;
        border-top-left-radius: 3px;
        border-bottom-left-radius: 3px;
    }

    .chat-app-content .controller input[type="text"]:focus {
        border: 1px solid #24a9ed;
    }

    .chat-app-content .controller input[type="text"]::placeholder {
        font-size: 12px;
    }

    .chat-app-content .controller button#send-message {
        display: block;
        padding: 13px 18px;
        background-color: #24a9ed;
        color: #000000;
        border: none;
        outline: none;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
    }

    .chat-app-content .controller button#send-message i {
        color: #ffffff;
    }

    .logout-container button#logout {
        background-color: #ffffff;
        color: #000000;
        border: none;
        outline: none;
        padding: 8px 18px;
        font-size: 13px;
        border-radius: 3px;
    }

    .logout-container button#logout:hover {
        cursor: pointer;
        background-color: #6763E7;
        color: #ffffff;
        transition: all 0.4s ease 0s;
    }

    @media screen and (max-width: 768px) {
        
        .login-container {
            width: 100%;
            margin: 100px auto 100px auto;
        }

        .login-container .login-content {
            width: 90%;
            margin: auto;
        }

        #main-form input[type="text"],
        #main-form input[type="password"] {
            width: 100%;
            margin: auto;
            border: none;
            outline: none;
