            border: none;
            outline: none;
            border-radius: 3px;
        }

        .chat-app-container {
            width: 90%;
            margin: 100px auto 100px auto;
        }

        .chat-app-content .logout-container {
            flex-direction: column;
        }

        .chat-app-content .controller {
            flex-direction: column;
        }

        .chat-app-content .controller input[type="text"]::placeholder {
            text-align: center;
        }

        .chat-app-content .controller button#send-message {
            display: block;
            padding: 13px 18px;
            background-color: #24a9ed;
            color: #000000;
            border: none;
            outline: none;
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
            width: 95%;
            margin: 8px auto auto auto;
        }
    }

</style>